// Colors
// ---------------------------------

// Default

$color-default-black:               #000000;
$color-default-white:               #ffffff;
$color-transparent:                 rgba(255, 255, 255, 0);

// Project palette
//     https://www.htmlcsscolor.com/hex/334482

$color-neon-blue:                   #2c39f2;
$color-torch-red:                   #ff1553;
$color-sapphire:                    #102654;
$color-venetian-red:                #b80027;
$color-dark-cerulean:               #003d85;
$color-ghost-white:                 #f8f9fc;
$color-crimson:                     #d10a42;
$color-chambray:                    #495a7d;
$color-whisper:                     #e8e8e8;
$color-egyptian-blue:               #103aae;
$color-torch-red2:                  #ed0233;
$color-cobalt:                      #003eb7;
$color-smalt:                       #00359f;
$color-dark-gray:                   #afafaf;
$color-resolution-blue:             #1c3374;
$color-cobalt:                      #0041ca;
$color-persian-blue:                #2e35ac;
$color-light-slate-blue:            #74819c;
$color-black-russian:               #030a1a;

// Gradient

$black-to-right:                    linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 75%);
$red-to-blue:                       linear-gradient(97.05deg, $color-torch-red2 17.6%, $color-cobalt 95.31%);
$red-to-blue-border:                linear-gradient(145deg, $color-crimson 0%, $color-smalt 100%);
$red-to-blue-number:                linear-gradient(142.51deg, $color-crimson 0%, $color-cobalt 87.33%);
$blue-to-red-desktop:               conic-gradient(from 150.87deg at 16.03% 39.06%, $color-venetian-red -46.69deg, $color-dark-cerulean 32.24deg, $color-venetian-red 313.31deg, $color-dark-cerulean 392.24deg);
$blue-to-red-tablet:                conic-gradient(from 155.49deg at 20.7% 35.74%, $color-venetian-red -46.69deg, $color-dark-cerulean 32.24deg, $color-venetian-red 313.31deg, $color-dark-cerulean 392.24deg);
$blue-to-red-mobile:                conic-gradient(from 167.26deg at 35.31% 34.22%, $color-venetian-red -46.69deg, $color-dark-cerulean 32.24deg, $color-venetian-red 313.31deg, $color-dark-cerulean 392.24deg);

// Typography
// ---------------------------------

//    html   
$fz-default:                        16px;

//     ios     
// https://www.artlebedev.ru/kovodstvo/sections/159/#13
$ff-rouble:                         "Rouble", "Arial", sans-serif;

$ff-alumni-sans:                    "Alumni Sans", "Arial", sans-serif;
$ff-roboto-condensed:               "Roboto Condensed", "Arial", sans-serif;

// Animation
// ---------------------------------

$tf-default:                        ease;
$trans-default:                     0.3s $tf-default;

//      $tf-default,     
$trans-modal:                       0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600:                         0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-320:                            320px;
$vp-375:                            375px;
$vp-768:                            768px;
$vp-1024:                           1024px;
$vp-1200:                           1200px;
$vp-1280:                           1280px;
$vp-1440:                           1440px;
$vp-1920:                           1920px;

// Retina
// ---------------------------------

$retina-dpi:                        144dpi;
$retina-dppx:                       1.5dppx;
